.body-overlay {
  opacity: 0;
  visibility: hidden;

  position: fixed;
  z-index: 10;
  inset: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0,0,0,.5);

  transition-property:  opacity, visibility;
  transition-duration: 500ms;

  &--active {
    opacity: 1;
    visibility: visible;
  }
}